import React from 'react'

const Navbar = () => {
  return (
    <nav className='flex justify-between items-center px-4 '>
        <div className='border '>
            <p>logo</p>
        </div>
        <div className='border '>
            <ul>
                <li>Explore</li>
                <li>Contact</li>
                <li>Support</li>
            </ul>
        </div>
        <div className='border '>
            <ul>
                <li>Login</li>
                <li>Sign Up</li>
            </ul>
        </div>
    </nav>
  )
}

export default Navbar